  position: relative;
  background: transparent;
}
.slider__range-controls::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 6px;
  top: 0;
  left: 0;
  background-color: #68b738;
  border-radius: 5px;
  margin: 3px 5px 3px;
