  display: none;
}
.slider__before:checked ~ .slider__scale .slider__range-controls {
  left: 0;
}
.slider__after:checked ~ .slider__wrap .slider__image-after {
  display: block;
}
.slider__after:checked ~ .slider__wrap .slider__image-before {
  display: none;
}
